<template>
  <div class="print-inner">
    <h3 class="print-check-title">临证治疗笺</h3>
    <patient-info :patient-data="patientData"/>
    <div class="print-linzheng-box">
      <ul>
        <li>处方日期</li>
        <li>治疗名称</li>
        <li>金额</li>
        <li>执行时间</li>
        <li>执行人签字</li>
      </ul>
      <ul v-for="(item, index) in lzList" :key="index">
        <li> {{ item.sysupdatedate }} </li>
        <li>{{ item.detailItemName }}</li>
        <li>{{ item.price }}</li>
        <li> &nbsp; </li>
        <li> &nbsp; </li>
      </ul>
    </div>
    <div style="overflow: hidden; clear: both">
      <div class="print-linzheng-footer">
      <span>金额：{{ sumPrice }}</span>
      <span>医生：{{patientData.userName}}</span>
      <span style="width: 300px">审核/调配签名：</span>
    </div>
    </div>
  </div>
</template>

<script>
import patientInfo from '../../components/print-patientInfo'
import {
  getEmpBasicMainEntyTable,
  getYiCunMedicareCure
} from '../../api'
export default {
  components: {
    patientInfo
  },
  data () {
    return {
      patientName: '',
      patientSex: '',
      patientAge: '',
      deptName: '',
      registrationNum: '',
      patientData: {},
      lzList: [],
    }
  },
  computed: {
    sumPrice () {
      if (this.lzList.length) {
        let totle = 0
        this.lzList.forEach( item => {
          totle = totle + parseInt(item.price)
        })
        return totle
      }else{
        return 0
      }
    }
  },
  methods: {
    getPatintData () {
      getEmpBasicMainEntyTable({ registrationNum: this.registrationNum }).then(res=> {
        if(res.data.code == '0'){
          this.patientData = res.data.data[0]
          this.patientData.emSex = this.patientData.emSex === '1' ? '男' : '女'
          this.getCheckData()
        }else{
          this.$message.error('获取患者信息失败！')
        }
      })
    },
    getCheckData () {
      getYiCunMedicareCure({ empBasicMainID: this.patientData.empBasicMainID }).then(res=>{
        if (res.data.code == '0') {
          this.lzList = res.data.data
        }else{
          this.$message.error('获取检测内容失败！')
        }
      })
    }
  },
  mounted () {
    let id= this.$route.query.id
    if (id) {
        this.registrationNum = id
       this.getPatintData()
    }else{
      this.$message.error('访问参数不正确！')
    }
  }
}
</script>

<style>
.print-inner{
  width: 960px;
  padding-left: 40px;
}
.print-check-title{
  padding: 24px 0;
  width: 100%;
  font-size: 26px;
  font-weight: normal;
  color: #000;
  text-align: center;
}
.print-linzheng-box{
  padding: 0 10px;
}
.print-linzheng-box ul{
  width: 100%;
}
.print-linzheng-box ul li{
  float: left;
  width: 20%;
  line-height: 30px;
  text-align: center;
}
.print-linzheng-footer{
  display: flex;
  margin-top: 38px;
  width: 100%;
  padding: 10px 0;
  justify-content:space-between;
  border-top: 1px solid #000;
}
</style>



